#registerContainer {
  background-color: $divider-color-light;
  min-height: calc(100vh - 77px);

  .container {
    padding: 120px 0;
  }
}

#registerDescription {
  li {
    list-style: none;
    color: $secondary-text-color;
    font-weight: 300;
    font-size: 28px;
    padding: 10px;

    i {
      font-family: FontAwesome;
      padding: 10px;
      color: $accent-color;
    }
  }
}

#registerForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  input {
    font-family: $Rale;
    font-weight: 400;
  }

  label {
    width: 100%;
    padding: 20px;
    font-family: $Mont;
    color: $secondary-text-color;
    font-weight: 600;
    text-transform: uppercase;
  }

  button {
    background-color: $primary-color;
    color: white;
  }
}

.registerHeading {
  margin-bottom: 60px;
}

@media screen and (max-width: 360px) {
  #registerContainer {

    .container {
      padding: 20px 0;
      width: calc(100% - 20px);
    }
  }

  #registerDescription {
    margin-bottom: 30px;
    justify-content: center;

    li {
      font-size: 1.2em;
      padding: 5px;
      width: auto;

      i {
        padding: 5px;
      }
    }
  }

  .registerHeading {
    width: 100%;
    text-align: center;
    font-size: 1.5em;
    margin-bottom: 10px;
  }

  #registerForm {
    label {
      padding: 5px;
    }
  }
}



