#singleHero {
  width: 100%;
  height: 60vh;
  position: relative;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .container {
    justify-content: space-between;
    position: absolute;
    bottom: 10px; left: 50%;
    transform: translateX(-50%);
    padding: 0;
  }
}

.apartmentHeroInfo {
  background-color: $primary-color-dark;
  color: $primary-color-text;
  box-shadow: 0 1px 2px rgba(black, 0.1);
  padding: 10px 20px;
  width: 20%;
  text-align: center;
}

.primaryInfo {
  margin: 0;

  div {
    display: flex;
    align-items: center;
  }

  span {
    font-family: $Slab;
    text-transform: capitalize;
  }

  i:before {
    margin: 0;
    padding-right: 20px;
    font-size: 1.5em;
    color: $accent-color;
  }

  &:nth-child(2) {
    justify-content: space-around;
  }
}

#singleContact {
  display: flex;
  padding: 60px 0;
  flex-direction: column;
  background-color: $divider-color-dark;

  .container {
    padding: 0;
  }
}

#ownerInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;

  h3 {
    color: $accent-color;
  }

  img {
    width: 200px;
    height: 200px;
    border: 6px solid $accent-color;
    border-radius: 50%;
    margin-bottom: 20px;
    object-fit: cover;
  }

  .half-section {
    align-items: baseline;
    font-family: $Mont;
    letter-spacing: -1px;
    margin: 20px 0 0 0;
    color: $primary-color-text;

    i:before {
      font-size: 32px;
      margin: 0;
      padding-right: 20px;
      color: $accent-color;
    }
  }
}

#singleDescription {
  p {
    margin: 0;
  }
}

#singleSecondary {
  background-color: $divider-color-light;
}

.secondaryInfo {
  padding-bottom: 60px;

  .subsection {
    margin: 0;
  }

  h3 {
    display: flex;
    flex-direction: column;
  }

  i:before {
    margin: 0;
    font-size: 38px;
    color: $accent-color;
  }
}

.singleGallery {
  figure {
    margin-bottom: 7px;
    flex-direction: column;
    box-shadow: 0 1px 2px rgba(black, 0.3);

    img {
      height: 250px;
    }

    figcaption {
      padding: 8px;
      background-color: $primary-color;
    }
  }
}

@media screen and (max-width: 360px) {
  #singleHero {
    height: 30vh;
  }

  .apartmentHeroInfo {
    width: auto;
  }

  #singlePrimary {

    .container {
      width: calc(100% - 20px);
      padding: 20px 0;
    }

    .subsection {
      width: 100%;
    }

    .half-section {
      flex-direction: column;
    }
  }

  #singleContact {
    padding: 10px 0;
  }

  #ownerInfo {

    img {
      width: 100px;
      height: 100px;
      border: 3px solid $accent-color;
    }

    .half-section {
      margin-top: 10px;
      letter-spacing: 0;
      font-weight: 300;
      font-size: 0.95em;

      i:before {
        font-size: 1em;
        padding-right: 5px;
      }
    }
  }

  #singleDescription {
    .container {
      padding: 10px;
    }
  }

  #singleSecondary {

    .secondaryInfo {
      padding: 0;
      margin-bottom: 10px;

      .subsection {
        margin-bottom: 10px;
      }
    }

    .singleGallery {
      width: calc(100% - 20px);
    }

    .singleImg {
      width: calc(50% - 5px);

      img {
        height: 100px;
      }

      figcaption {
        font-size: 0.85em;
        padding: 5px;
      }
    }
  }
}



